"use client";

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { PageHeader } from '@/components/PageHeader';
import { ExpenseForm } from '@/components/ExpenseForm';
import { Button } from '@/components/ui/button';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
  TableCaption,
} from '@/components/ui/table';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogTrigger,
} from '@/components/ui/dialog';
import { PlusCircle, Edit, Trash2, Loader2 } from 'lucide-react';
import type { Expense } from '@/types';
import { formatCurrency, formatDate } from '@/lib/utils';
import { CATEGORY_ICONS } from '@/lib/constants';
import usePersistentState from '@/hooks/usePersistentState';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/contexts/AuthContext'; // Import useAuth

export default function ExpensesPage() {
  const { session, isLoadingAuth } = useAuth();
  const router = useRouter();

  // This will be replaced with Supabase data fetching later
  const [expenses, setExpenses] = usePersistentState<Expense[]>('moneywise-expenses', []);
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingExpense, setEditingExpense] = useState<Expense | null>(null);
  const [mounted, setMounted] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    if (mounted && !isLoadingAuth && !session) {
      router.push('/auth/signin');
    }
  }, [session, isLoadingAuth, router, mounted]);

  const handleAddExpense = (newExpense: Expense) => {
    // TODO: Save to Supabase
    setExpenses((prevExpenses) => [newExpense, ...prevExpenses]);
    setIsFormOpen(false);
    toast({ title: "Expense Added", description: `${newExpense.description} successfully added.` });
  };

  const handleUpdateExpense = (updatedExpense: Expense) => {
    // TODO: Update in Supabase
    setExpenses((prevExpenses) =>
      prevExpenses.map((exp) => (exp.id === updatedExpense.id ? updatedExpense : exp))
    );
    setEditingExpense(null);
    setIsFormOpen(false);
    toast({ title: "Expense Updated", description: `${updatedExpense.description} successfully updated.` });
  };

  const handleDeleteExpense = (expenseId: string) => {
    // TODO: Delete from Supabase
    if (confirm("Are you sure you want to delete this expense?")) {
      const expenseToDelete = expenses.find(exp => exp.id === expenseId);
      setExpenses((prevExpenses) => prevExpenses.filter((exp) => exp.id !== expenseId));
      if (expenseToDelete) {
        toast({ title: "Expense Deleted", description: `${expenseToDelete.description} successfully deleted.`, variant: "destructive" });
      }
    }
  };

  const openEditForm = (expense: Expense) => {
    setEditingExpense(expense);
    setIsFormOpen(true);
  };

  const openNewForm = () => {
    setEditingExpense(null);
    setIsFormOpen(true);
  }

  const sortedExpenses = [...expenses].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());

  if (isLoadingAuth || !mounted || !session) {
     return (
      <div className="flex min-h-[calc(100vh-150px)] flex-col items-center justify-center p-4">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="mt-4 text-muted-foreground">Loading Expenses...</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <PageHeader
        title="Manage Expenses"
        description="Track and categorize your spending efficiently."
        actions={
          <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
            <DialogTrigger asChild>
              <Button onClick={openNewForm} className="bg-primary hover:bg-primary/90 text-primary-foreground">
                <PlusCircle className="mr-2 h-5 w-5" /> Add New Expense
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-lg">
              <DialogHeader>
                <DialogTitle>{editingExpense ? 'Edit Expense' : 'Add New Expense'}</DialogTitle>
                <DialogDescription>
                  {editingExpense ? 'Update the details of your expense.' : 'Fill in the details to add a new expense.'}
                </DialogDescription>
              </DialogHeader>
              <ExpenseForm
                onSubmit={editingExpense ? handleUpdateExpense : handleAddExpense}
                initialData={editingExpense}
                onCancel={() => {
                  setIsFormOpen(false);
                  setEditingExpense(null);
                }}
              />
            </DialogContent>
          </Dialog>
        }
      />

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>Expense List (Sample Data)</CardTitle>
          <CardDescription>View and manage your recorded expenses. Data is currently local.</CardDescription>
        </CardHeader>
        <CardContent>
          {sortedExpenses.length === 0 ? (
            <div className="py-10 text-center text-muted-foreground">
              <p className="text-lg">No expenses recorded yet.</p>
              <p>Click "Add New Expense" to get started.</p>
            </div>
          ) : (
            <Table>
              <TableCaption>A list of your recent expenses.</TableCaption>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[100px]">Date</TableHead>
                  <TableHead>Description</TableHead>
                  <TableHead>Category</TableHead>
                  <TableHead className="text-right">Amount</TableHead>
                  <TableHead className="text-center w-[100px]">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {sortedExpenses.map((expense) => {
                  const IconComponent = CATEGORY_ICONS[expense.category] || CATEGORY_ICONS["Others"];
                  return (
                    <TableRow key={expense.id} className="hover:bg-muted/50">
                      <TableCell>{formatDate(expense.date, 'dd MMM')}</TableCell>
                      <TableCell className="font-medium">{expense.description}</TableCell>
                      <TableCell>
                        <span className="flex items-center gap-2">
                          <IconComponent className="h-4 w-4 text-muted-foreground" />
                          {expense.category}
                        </span>
                      </TableCell>
                      <TableCell className="text-right">{formatCurrency(expense.amount)}</TableCell>
                      <TableCell className="text-center">
                        <Button variant="ghost" size="icon" onClick={() => openEditForm(expense)} className="mr-1 text-blue-500 hover:text-blue-700">
                          <Edit className="h-4 w-4" />
                          <span className="sr-only">Edit</span>
                        </Button>
                        <Button variant="ghost" size="icon" onClick={() => handleDeleteExpense(expense.id)} className="text-red-500 hover:text-red-700">
                          <Trash2 className="h-4 w-4" />
                           <span className="sr-only">Delete</span>
                        </Button>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
}